<template>
  <DashBoardView>
    <template #slot-page>
      <div class="content-pages p-6">
        <header class="title_pages mb-4">
          <p class="text-2xl font-bold">Inicio</p>
        </header>
        <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
          <CardComponent
            :type="'Clientes'"
            :percentage="'7%'"
            :icon="'fa-users'"
            :qtd="users.length"
          />
          <CardComponent
            :type="'Produtos'"
            :percentage="'12%'"
            :icon="'fa-box'"
            :qtd="'350'"
          />
          <CardComponent
            :type="'Serviços'"
            :percentage="'3%'"
            :icon="'fa-store'"
            :qtd="'270'"
          />
          <CardComponent
            :type="'Relatórios'"
            :percentage="'25%'"
            :icon="'fa-chart-bar'"
            :qtd="'30'"
          />
        </div>

        <div class="grid grid-cols-1 lg:grid-cols-2 gap-4">
          <ListsComponent :users="users" />
          <ListsComponent :users="users" />
        </div>
      </div>
    </template>
  </DashBoardView>
</template>
<script>
import CardComponent from '@/components/CardComponent.vue';
import ListsComponent from '@/components/ListsComponent.vue';
import DashBoardView from '@/views/DashBoard/DashBoardView.vue';
import axios from 'axios';
export default {
  name: 'HomeView',
  components: {
    DashBoardView,
    CardComponent,
    ListsComponent,
  },
  data() {
    return {
      qtd: [],
      users: [],
    };
  },
  methods: {
    async getUsers() {
      try {
        const response = await axios.get(
          'https://jsonplaceholder.typicode.com/users'
        );
        this.users = response.data;
        return this.qtd, this.users;
      } catch (e) {
        console.log(e);
      }
    },
  },
  mounted() {
    this.getUsers();
  },
};
</script>
<style lang="scss" scoped>
@import '@/assets/scss/pages/home';
</style>
